@font-face {
  font-family: 'Appetite New';
  src: url('src/fonts/AppetiteNew/AppetiteNew.eot');
  src: url('src/fonts/AppetiteNew/AppetiteNew.eot?#iefix') format('embedded-opentype'),
      url('src/fonts/AppetiteNew/AppetiteNew.woff2') format('woff2'),
      url('src/fonts/AppetiteNew/AppetiteNew.woff') format('woff'),
      url('src/fonts/AppetiteNew/AppetiteNew.ttf') format('truetype'),
      url('src/fonts/AppetiteNew/AppetiteNew.svg#AppetiteNew') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

